export default "@import '../../themes/src/default.css';\n\n.customButton {\n    overflow: hidden;\n    position: relative;\n    border: 0;\n\n    &.white * {\n        color: var(--color-light-graphic-primary-inverted);\n    }\n\n    &.black * {\n        color: var(--color-light-graphic-primary);\n    }\n\n    &.static-white * {\n        color: var(--color-static-graphic-light);\n    }\n\n    &.static-black * {\n        color: var(--color-static-graphic-dark);\n    }\n\n    & span {\n        position: relative;\n        z-index: 3;\n    }\n\n    &:before {\n        position: absolute;\n        left: 0;\n        right: 0;\n        top: 0;\n        bottom: 0;\n        background-color: rgba(0, 0, 0, 0);\n        content: '';\n        transition: 0.2s;\n        opacity: 1;\n        pointer-events: none;\n    }\n\n    &.darkening {\n        &:hover:before {\n            background-color: var(--color-light-border-key-alpha-7);\n        }\n\n        &:active:before {\n            background-color: var(--color-light-border-key-alpha-15);\n        }\n    }\n\n    &.lightening {\n        &:hover:before {\n            background-color: var(--color-light-border-key-inverted-alpha-7);\n        }\n\n        &:active:before {\n            background-color: var(--color-light-border-key-inverted-alpha-15);\n        }\n    }\n\n    &.static-darkening {\n        &:hover:before {\n            background-color: rgba(0, 0, 0, 0.07);\n        }\n\n        &:active:before {\n            background-color: rgba(0, 0, 0, 0.15);\n        }\n    }\n\n    &.static-lightening {\n        &:hover:before {\n            background-color: rgba(255, 255, 255, 0.07);\n        }\n\n        &:active:before {\n            background-color: rgba(255, 255, 255, 0.15);\n        }\n    }\n}\n\n/* Disabled */\n\n.customButton:disabled {\n    opacity: 0.3;\n\n    &.customLoading {\n        opacity: 1;\n    }\n\n    &.white * {\n        color: var(--color-static-text-primary-light);\n    }\n\n    &.black * {\n        color: var(--color-static-text-primary-dark);\n    }\n}\n";